.photo-wave {
  &-container {
    display: flex;
    flex-wrap: nowrap;
    -webkit-box-pack: center;
    justify-content: center;
    width: 100%;
    overflow: hidden;
    padding: 80px 0px 180px;

    margin-top: -70px;

    & > * {
      flex-shrink: 0;
    }

    > :not(:last-child) {
      margin-right: 12px;
    }
    > :nth-child(1) {
      margin-top: 90px;
    }
    > :nth-child(2) {
      margin-top: 50px;
    }
    > :nth-child(4) {
      margin-top: 40px;
    }
    > :nth-child(5) {
      margin-top: 90px;
    }
    > :nth-child(6) {
      margin-top: 50px;
    }
  }

  &-relative-wrapper {
    position: relative;
    height: 150px;
    width: 120px;
  }

  &-img-wrapper {
    border-radius: 10px;
    overflow: hidden;
    height: 100%;
    width: 100%;
    position: relative;
    img {
      height: 100%;
      object-fit: cover;
      width: 100%;
    }
  }
}
